/* el css tiene la misma specs que el item porque tiene que ser igual solo que sin contenido */
.LoadingTodo-container{
    background-color: #fafafa;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    padding: 12px 0px;    
}

.LoadingTodo-text{
    margin: 24px 0px 24px 24px;
    width: calc(100%-120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        height: 48px;
        width: 48px;
}

.LoadingTodo-completeIcon{
    position: absolute;
    left: 12px;
}

.LoadingTodo-deleteIcon{
    position: absolute;
    top: -24px;
    right: 0;
}

.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon{
    background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200,199,199));background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes loadingAnimation{
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50% ;
    }
}